          <el-radio label="无"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="ruleForm.ganran.key">
        <el-radio-group v-model="ruleForm.ganran.value">
          <el-radio label="有"></el-radio>
          <el-radio label="无"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="ruleForm.jiehe.key">
        <el-radio-group v-model="ruleForm.jiehe.value">
          <el-radio label="有"></el-radio>
          <el-radio label="无"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="ruleForm.smoke.key">
        <el-radio-group v-model="ruleForm.smoke.value">
          <el-radio label="有"></el-radio>
          <el-radio label="无"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="ruleForm.cat.key">
        <el-radio-group v-model="ruleForm.cat.value">
          <el-radio label="有"></el-radio>
          <el-radio label="无"></el-radio>
