<script>
// @ is an alias to /src

export default {
  data() {
    return {
      ruleForm: {
        resource: {
          key: "一.有无介入放射科病史",
          value: "",
        },
        gettime: {
          key: "二.患病时间",
          value: "",
        },
        reason: {
          key: "三.是否是肿瘤患者",
          value: "",
        },
        more: {
          key: "四.有无接触化学物质、放射线",
          value: "",
        },
        ganran: {
          key: "五.有无病毒感染",
          value: "",
        },
        jiehe: {
          key: "六.有无内分泌、遗传等方面疾病",
          value: "",
        },
        smoke: {
          key: "七.有无烟酒嗜好",
          value: "",
        },
        cat: {
          key: "八.是否是血管病患者",
          value: "",
        },
        types: {
          key: "九.有无以下病史（可多选）",
          value: [],
        },
      },
